.widgetLg {
    flex: 2;
    border-radius: .4rem;
    margin: 20px 40px;
    padding: 20px;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.widgetLgTable {
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTitle {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 20px;
}

.widgetLgTr {
    text-align: left;
    
}

.widgetLgUser {
    display: flex;
    align-items: center;
    column-gap: .8rem;
    font-weight: 600;
}

.widgetLgImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetLgDate, 
.widgetLgAmount {
    font-weight: 300;
}

.widgetLgBtn {
    padding: 5px 7px;
    border-radius: .5rem;

}

.widgetLgBtn.Approved{
    background-color: #e5faf2;
    color: #3bb077;
}
.widgetLgBtn.Declined{
    background-color: #fff0f1;
    color: #d95087;

}
.widgetLgBtn.Pending{
    background-color: #ebf1fe;
    color: #2a7ade;

}